@import url(https://fonts.googleapis.com/css?family=Quantico);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@charset "UTF-8";

html, body {
    background: #111;
}

#container {
    background: #111;
    position: absolute;
    left: 50%;
    top: 50%;
}

#stats {
    position: absolute;
    right: 10px;
    top: 10px;
}

/* Info */
@-webkit-keyframes show-info {
    0% {
        transform: rotateY(120deg);
    }

    100% {
        transform: rotateY(0deg);
    }
}
@keyframes show-info {
    0% {
        transform: rotateY(120deg);
    }

    100% {
        transform: rotateY(0deg);
    }
}

.info {
    transition: all 180ms ease-out;
    transform-style: preserve-3d;
    transform: perspective(800px);
    font-family: "Quantico", sans-serif;
    position: absolute;
    font-size: 12px;
    opacity: 0.8;
    color: #fff;
    width: 220px;
    left: 0px;
    top: 20px;
}

    .info:hover {
        box-shadow: 0 0 0 4px rgba(255, 255, 255, 0.05);
        opacity: 1;
    }

    .info h1, .info h2, .info h3 {
        line-height: 1;
        margin: 5px 0;
    }

    .info a {
        transition: all 200ms ease-out;
        border-bottom: 1px dotted rgba(255, 255, 255, 0.4);
        text-decoration: none;
        opacity: 0.6;
        color: #fff;
    }

        .info a:hover {
            opacity: 0.99;
        }

    .info .about,
    .info .more {
        transform-origin: 0% 50%;
        transform: rotateY(120deg);
        margin-bottom: 1px;
        background: rgba(0, 0, 0, 0.8);
        padding: 12px 15px 12px 20px;
    }

    .info .about {
        -webkit-animation: show-info 500ms cubic-bezier(0.23, 1, 0.32, 1) 600ms 1 normal forwards;
                animation: show-info 500ms cubic-bezier(0.23, 1, 0.32, 1) 600ms 1 normal forwards;
        padding-bottom: 15px;
    }

        .info .about a {
            opacity: 0.9;
        }

        .info .about h1 {
            letter-spacing: -1px;
            font-weight: 300;
            font-size: 19px;
            opacity: 0.95;
        }

        .info .about h2 {
            font-weight: 300;
            font-size: 13px;
            opacity: 0.8;
        }

        .info .about h3 {
            text-transform: uppercase;
            margin-top: 10px;
            font-size: 11px;
        }

            .info .about h3:before {
                margin-right: 2px;
                font-size: 14px;
                content: "�";
            }

    .info .more {
        -webkit-animation: show-info 500ms cubic-bezier(0.23, 1, 0.32, 1) 500ms 1 normal forwards;
                animation: show-info 500ms cubic-bezier(0.23, 1, 0.32, 1) 500ms 1 normal forwards;
        padding: 5px 15px 10px 20px;
    }

        .info .more a {
            text-transform: uppercase;
            margin-right: 10px;
            font-size: 10px;
        }

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

